.modal-blog.modal,
.modal-blog.overlay{
    overflow:scroll;
}

.modal-blog.modal .modal-content{
    position: relative;
    top: 10%;
    left: 0;
    max-width: 70%;
    margin: auto;
    margin-bottom: 10%;
}
.modal-blog .modal-content .right,
.modal-blog .modal-content .left{
    width: 100%;
    padding: 0;
    margin-top: 30px;
}
.grid1{
    display: grid;
    grid-template-columns: repeat(4,1fr);
}
.blog_contact .right,
.blog_contact .left{
    margin: 0 25px 0 0;
}
@media (max-width:768px) {
    .modal-blog.modal .modal-content{
        max-width: 90%;
        margin-bottom: 20%;
    }
}