@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
*{
  padding: 0;
  margin: 0;
}
html{
  scroll-behavior: smooth;
}
body{
  font-family: 'Poppins', sans-serif;
  background-color: #ecf0f3;
}
.container{
  min-width: 95%;
  margin: auto;
}
.d_flex{
  display: flex;
  justify-content: space-between;
}
.f_flex{
  display: flex;
}
a{
  text-decoration: none;
  color: black;
}
li{
  list-style: none;
  margin-right: 10px;
}
.uppercase{
  text-transform: uppercase;
}
.capitalize{
  text-transform: capitalize;
}
button{
  border: none;
  outline: none;
  background-color: none;
}
.btn_shadow{
  padding: 18px 25px;
  border-radius: 6px;
  transition: 0.5s all ease;
  cursor: pointer;
  color: #000;
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 4px 4px 8px #cbcbcb, 4px -4px 8px #ffffff;
}
.btn_shadow:hover{
  color: white;
  background: #ff014f;
  transform: translateY(-10px);
  cursor: pointer;
  transition: 1.5s;
  box-shadow: 4px 4px 8px #878e99, -4px 4px 8px #878e99;
}
.box_shadow{
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 4px 4px 8px #cbcbcb, 4px -4px 8px #ffffff;
  border-radius: 10px;
}
.row, .right, .left{
  width: 50%;
}
.top{
  margin-top: 80px;
}
.mtop{
  margin-top: 40px;
}

.heading{
  margin: 50px 0 50px 0;
}
.heading h4{
  color: #ff014f;
  letter-spacing: 2px;
  font-weight: 400;
}
.heading h1{
  font-size: 60px;
  color: #3c3e41;
}
h1{
  color: #3c3e41;
}
.grid{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-gap: 35px;
}
p{
  color: #3c3e41;
}
.text-center{
  text-align: center;
}
textarea,
  input{
    width: 500px;
    padding: 20px;
    border-radius: 5px;
    outline: none;
    border: 3px solid #e2e8ec;
  }

footer{
  margin: 20% 0 10% 0;
}  
@media (max-width:768px) {
  .grid{
    grid-template-columns: repeat(2,1fr);
  }
  .left,
  .right{
    width: 100%;
  }
  
}