.Resume .heading h1{
    font-size: 40px;
}
.Resume .content{
    max-width: 85%;
    margin: auto;
}
.Resume .box{
    padding: 40px;
}
.Resume hr{
    margin: 30px 0 30px 0;
    border: 1px solid #e3e9ed;
    background-color: #e3e9ed;
}
.Resume .rate .btn_shadow{
    color: #ff014f;
    padding: 15px 30px;
    font-weight: bold;
    transition: 0.5s;
}
.Resume p{
    font-size: 18px;
    transition: 0.5s;
}

.Resume .box{
    margin-bottom: 40px;
    position: relative;
}
.Resume .btn_shadow:hover{
    transform: translate(0);
}
.Resume .box:hover .btn_shadow{
    background: #ff014f;
    box-shadow: none;
    color: white;
}
.Resume .box:hover p{
    color: white;
}
.Resume .content-section{
    position: relative;
}
.Resume .left{
    position: relative;
}
.Resume .left::after{
    content: '';
    position: absolute;
    top: 16%;
    left: 0;
    height: 90vh;
    width: 5px;
    background: #d9dee2;
    z-index: -2;
}
.Resume .box::after{
    content: '';
    position: absolute;
    top: 13%;
    left: -8%;
    height: 5px;
    width: 50px;
    background: #d9dee2;
}

.Resume .box::before{
    content: '';
    position: absolute;
    top: 11%;
    left: -9.5%;
    height: 12px;
    width: 12px;
    background: white;
    border:2px solid #d9dee2 ;
    border-radius: 50%;
    z-index: 5;
    transition: 0.5s;
}
.Resume .box:hover.box:before{
background: #ff014f;
}

@media (max-width:768px) {
    .Resume .d_flex{
        flex-direction: column;
    }
    .Resume .left::after{
        top: 14%;
        height: 105vh;
    }
}